export default {
	data() {
		return {
			title: '充值金额',
			current:-1,
			muchArr:[5,10,30,50],
			isFlag:[true,true,true,true],
			totolMuch:0,
			morePay:false
		}
	},
	onLoad() {

	},
	methods: {
		//选择金额
		selectMuch(je,i){
			this.current = i;
			this.totolMuch = je;
		},
		//输入金额，失去焦点拿到金额渲染到下面
		enterMuch(){
			this.totolMuch = this.inputMuch;
		},
		//点击微信支付
		weixin(){
			this.muchArr.forEach((item,index) => {
				if(index !=0){
					this.$set(this.isFlag,index,true)
				}
				this.$set(this.isFlag,0,false)
			})
		},
		//点击支付宝支付
		zhifubao(){
			this.muchArr.forEach((item,index) => {
				if(index !=1){
					this.$set(this.isFlag,index,true)
				}
				this.$set(this.isFlag,1,false)
			})
			
		},
		//点击银联支付
		yinlian(){
			this.muchArr.forEach((item,index) => {
				if(index !=2){
					this.$set(this.isFlag,index,true)
				}
				this.$set(this.isFlag,2,false)
			})
		},
		//点击更多支付
		gengduo(){
			this.muchArr.forEach((item,index) => {
				if(index !=3){
					this.$set(this.isFlag,index,true)
				}
				this.$set(this.isFlag,3,false)
			})
		},
		clickCheckMore(){
			this.morePay = true;
		}
	}
}
